<template>
    <div class="product">
        <div class="product-display" @mouseover="hoverDisplay = true" @mouseleave="hoverDisplay = false">
            <img src="../assets/alex-replacement-logo.png">
            <!-- Desktop only!!! -->
            <div class="product-display-left" v-bind:style="{ opacity: hoverDisplay == true ? '1' : '0' }">
                <img src="../assets/arrow.png">
            </div>
            <div class="product-display-right" v-bind:style="{ opacity: hoverDisplay == true ? '1' : '0' }">
                <img src="../assets/arrow.png">
            </div>
        </div>
        <p class="product-title">{{ displayName }}</p>
        <p class="product-desc">{{ displayDesc }}</p>
        <p class="product-price">{{ price }}</p>
    </div>
</template>

<script>
export default {
    name: 'ProductDisplay',
    
    props: {
        name: String,
        description: String,
        price: String
    },
    
    data() {
        return {
            hoverDisplay: false
        }
    },
    
    created() {
        console.log(this.name);
        
        this.displayName = this.name;
        this.displayDesc = this.description;
        
        /*
        if images.length == 1 then
            delete product-display-left
            delete product-display-right
        
        */
        
        /*if(this.displayName.length > 40) { this.displayName = this.displayName.slice(0, 40) + '...'; }
        if(this.displayDesc.length > 64) { this.displayDesc = this.displayDesc.slice(0, 64) + '...'; }*/
    },
    
    data() {
        return {
            displayName: 'displayName',
            displayDesc: 'displayDesc'
        }
    }
}
</script>


<style>

.product
{
    margin: 0px;
    padding: 0;
    
    position: relative;
    
    width: 300px;
    height: 0;
    padding-bottom: 45%;
    
    background-color: white;
    
    cursor: pointer;
}

/* The product display */
.product-display
{
    position: relative;
    
    width: calc(100% - 16px);
    height: auto;
    
    margin: 8px;
}

.product-display > img
{
    width: 100%;
    height: auto;
    
    margin-bottom: 0;
}

.product-display > div
{
    position: absolute;
    
    width: 32px;
    height: 32px;
    
    top: 50%;
    transform: translateY(-50%);
    
    background-color: rgba(87, 87, 87, 0.336);
    border-radius: 100%;
    
    opacity: 0;
    transition: opacity .1s ease-in-out;
}

.product-display > div > img
{    
    position: absolute;
    
    top: 50%;
    left: 50%;
    
    height: 16px;
    
    filter: invert(100%) sepia(11%) saturate(163%) hue-rotate(146deg) brightness(111%) contrast(72%);
}

.product-display-left
{
    left: 8px;
}

.product-display-left > img
{
    transform: translate(-50%, -50%) scaleX(-1);
}

.product-display-right
{
    right: 8px;
}

.product-display-right > img
{
    transform: translate(-50%, -50%);
}

.product > p 
{
    position: relative;
    
    text-align: left;
    margin: 8px;
    
    font-size: 16px;
}

.product-title
{
    color: black;
    font-weight: normal;
    
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1; /* number of lines to show */
    -webkit-box-orient: vertical;
}

.product-desc
{
    color: rgb(97, 97, 97);
    
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;
}

.product-price
{
    position: absolute;
    
    color: black;
    
    font-size: 20px;
    
    bottom: 12px;
}

</style>