<template>
    <div id="navbar" v-bind:style="{ transform: 'translateY(' + (navHidden == true ? '-100' : '0') + '%) !important' }">
        <div id="navbar-left-content">
            <img src="./assets/alex-logo.png">
        </div>
        <div id="navbar-middle-content">
            <router-link to="/clothing">Men</router-link>
            <a href="#">Women</a>
            <a href="#">Children</a>
            <a href="#">Design</a>
            <a href="#">About Us</a>
        </div>
        <div id="navbar-right-content">
            
            <!-- Desktop only -->
            <div id="navbar-search" class="desktop">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 256" style="enable-background:new 0 0 256 256;">
	                <path d="M251.3,228.7L171.7,149c10.9-15.4,17.3-34.2,17.3-54.5C189,42.3,146.7,0,94.5,0S0,42.3,0,94.5S42.3,189,94.5,189
		            c20.3,0,39.1-6.4,54.5-17.3l79.6,79.6c2.9,2.9,6.9,4.7,11.3,4.7c8.8,0,16-7.2,16-16C256,235.6,254.2,231.6,251.3,228.7z M29,94.5
		            C29,58.3,58.3,29,94.5,29S160,58.3,160,94.5c0,36.2-29.3,65.5-65.5,65.5S29,130.7,29,94.5z"/>
                </svg>
                
                <input type="text" placeholder="Search...">
            </div>
            
            <div id="navbar-mobile-search" v-on:click="navHidden = true" class="mobile">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 256" style="enable-background:new 0 0 256 256;">
	                <path d="M251.3,228.7L171.7,149c10.9-15.4,17.3-34.2,17.3-54.5C189,42.3,146.7,0,94.5,0S0,42.3,0,94.5S42.3,189,94.5,189
		            c20.3,0,39.1-6.4,54.5-17.3l79.6,79.6c2.9,2.9,6.9,4.7,11.3,4.7c8.8,0,16-7.2,16-16C256,235.6,254.2,231.6,251.3,228.7z M29,94.5
		            C29,58.3,58.3,29,94.5,29S160,58.3,160,94.5c0,36.2-29.3,65.5-65.5,65.5S29,130.7,29,94.5z"/>
                </svg>
            </div>
            
            <div id="navbar-shoppingcart">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 256" style="enable-background:new 0 0 256 256;">
                    <path d="M244.9,64H32c10.1,30.2,20.1,60.4,30.2,90.5c1.1,3.3,4.1,5.5,7.6,5.5h148.5c3.4,0,6.5-2.2,7.5-5.4
                	c8.9-26.7,17.8-53.3,26.7-80C254.2,69.4,250.3,64,244.9,64z"/>
                    <path d="M48,32c-16,0-32,0-48,0v32c21.3,0,42.7,0,64,0C58.7,53.3,53.3,42.7,48,32z"/>
                    <circle cx="200" cy="200" r="24"/>
                    <circle cx="88" cy="200" r="24"/>
                </svg>
            </div>
            
        </div>
    </div>
</template>

<script>
export default {
    name: 'Navbar',
    
    created() {
        /* For hiding and showing the navigation bar */
        window.addEventListener('scroll', this.onScroll);
    },
    
    destroyed() {
        window.removeEventListener('scroll', this.onScroll);
    },
    
    data() {
        return {
            navHidden: false,
            
            lastScroll: 0
        }
    },
    
    methods: {
        onScroll(e) {
            let value = this.lastScroll - window.pageYOffset;
            let threshold = 10;
            
            if(value > threshold) {
                this.navHidden = false;
            }
            else if(value < -threshold) {
                this.navHidden = true;
            }
            
            this.lastScroll = window.pageYOffset;
        }
    }
}
</script>


<style>

#navbar
{
    z-index: 999;
    
    font-size: 16px;
    
    position: fixed;
    
    height: 60px;
    width: 100%;
    
    background-color: white;
    
    box-shadow: 0 0 20px rgba(127, 127, 129, 0.233);
    
    
    transform: translateY(-0%);
    transition: transform .2s ease-in-out;
}

#navbar > div
{
    position: absolute;
    height: 100%;
        
    display: flex;
    align-items: center;
    
}

#navbar-left-content
{
    padding: 8px;
    
    left: 0;
    
    width: 300px;
}

#navbar-left-content > img
{
    height: 100%;
    width: auto;
}


/* The middle items */
#navbar-middle-content
{
    left: 50%;
    transform: translateX(-50%);
        
    width: 800px;
    
    /* Spacing the items */
    justify-content: space-between;
}

#navbar-middle-content > a
{
    color: black;
    text-decoration: none;
}

/* The right content */
#navbar-right-content
{   
    right: 0;
    
    padding: 8px;
    
    width: 300px;

    /* Putting all the items to the right */
    justify-content: flex-end;
}

/* The search bar */
#navbar-search
{    
    display: flex;
    justify-content: space-between;
    align-items: center;

    
    padding: 10px 10px;
    
    height: 100%;
    width: 200px;
    
    background-color: rgb(236, 236, 236);
    
    border-radius: 100px;
}

#navbar-search > input
{    
    margin-left: 8px;
            
    height: 28px;
    width: 100%;
    left: 44px;
    
    color: black;
    
    border: none;
    outline: none;
    
    background-color: transparent;
}

#navbar-search > svg
{            
    height: 24px;
    left: 10px;
    
    fill: rgb(58, 58, 58);
}

#navbar-mobile-search
{    
    padding: 10px;
    
    height: 44px;
    width: 44px;
    
    background-color: rgb(236, 236, 236);
    fill: rgb(58, 58, 58);
    
    border-radius: 100%;
}

/* The shoppingcart icon */

#navbar-shoppingcart
{
    margin-left: 8px;
    padding: 10px;
    
    height: 44px;
    width: 44px;
    
    background-color: rgb(236, 236, 236);
    fill: rgb(58, 58, 58);
    
    border-radius: 100%;
    
    cursor: pointer;
}

/* Desktop only */
@media only screen and (min-width: 1000px)
{
    .mobile
    {
        display: none !important;
    }
}

/* Smaller screens and phones */
@media only screen and (max-width: 1000px)
{   
    .desktop
    {
        display: none !important; 
    }

    
    #navbar
    {
        height: 70px;
    }
    
    #navbar-middle-content
    {
        display: none !important;
    }
}

</style>